  padding: 16px;
}
.v-dialog-container .v-dialog-header .v-dialog-title {
  font-size: 120%;
  font-weight: 500;
}
.v-dialog-container .v-dialog-body {
  overflow-y: auto;
  padding: 16px;
  position: relative;
}
.v-dialog-container .v-dialog-body .v-dialog-input {
  display: block;
  width: 100%;
}
.v-dialog-container .v-dialog-body .prompt-error-message {
  color: #d9534f;
  font-size: 14px;
}
.v-dialog-container .v-dialog-body .content textarea {
  height: 12vh;
  border: 0px;
  font-size: 13px;
  font-weight: 300;
  resize: none;
}
.v-dialog-container .v-dialog-footer {
  padding: 16px;
  text-align: right;
}
.v-dialog-container .v-dialog-btn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  color: #5cb85c;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  height: 36px;
  line-height: 20px;
  outline: none;
  padding: 7px 8px;
  text-align: center;
  text-decoration: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  font-weight: 400;
}
.v-dialog-container .v-dialog-btn.success {
  color: #50596c;
}
.v-dialog-container .v-dialog-btn.v-dialog-btn-danger {
  color: #d9534f;
}
.v-dialog-container .v-dialog-btn.v-dialog-btn-close {
  height: 16px;
  line-height: 16px;
  margin-left: 8px;
  margin-right: -15px;
  padding: 0;
  width: 16px;
  float: right;
  color: #50596c;
}
.v-dialog-container .v-dialog-btn.v-dialog-btn-close:before {
  content: "\2715";
}
.v-dialog-container .v-dialog-btn.disabled,
.v-dialog-container .v-dialog-btn:disabled,
.v-dialog-container .v-dialog-btn[disabled] {
  cursor: default;
  opacity: 0.5;
  pointer-events: none;
}@charset "utf-8";

