textarea { width: 100%; height: 70vh; padding: 20px; border: 1px solid #e5e5e5; color: #666; line-height: 17px; font-size: 12px; vertical-align: baseline; -moz-appearance: none; -webkit-appearance: none; -webkit-box-sizing: border-box; box-sizing: border-box; }
.checks { /*input[type="radio"]:focus+label, input[type="checkbox"]:focus+label{
	border: 1px dotted #000;
	//margin: -1px
}*/ /*      */ }
.checks input[type="radio"], .checks input[type="checkbox"] { position: absolute; width: 1px; height: 1px; padding: 0; margin: -1px; overflow: hidden; clip: rect(0, 0, 0, 0); border: 0; }
.checks input[type="radio"]:focus, .checks input[type="checkbox"]:focus { width: 20px; height: 20px; outline: solid; }
.checks input[type="radio"] + label { display: inline-block; position: relative; padding-left: 40px; cursor: pointer; color: #333; min-width: 0; font-weight: 700; font-size: 15px; line-height: 30px; text-align: left; vertical-align: middle; -webkit-user-select: text; -moz-user-select: text; -ms-user-select: text; margin-right: 40px; position: relative; }
.checks input[type="radio"] + label:before { position: absolute; left: 0; top: 0; display: inline-block; content: ""; width: 30px; height: 30px; vertical-align: middle; margin-right: 10px; background: url(data:image/png;base64,) no-repeat left top/30px; }
.checks input[type="radio"] + label:last-of-type { margin: 0; }
.checks input[type="checkbox"] + label { display: inline-block; position: relative; padding-left: 40px; cursor: pointer; color: #333; min-width: 0; font-weight: 700; font-size: 15px; line-height: 30px; text-align: left; vertical-align: middle; -webkit-user-select: text; -moz-user-select: text; -ms-user-select: text; }
.checks input[type="checkbox"] + label:before { position: absolute; left: 0; top: 0; display: inline-block; content: ""; width: 30px; height: 30px; vertical-align: top; margin-right: 10px; background: url(data:image/png;base64,) no-repeat left top/30px; }
.checks input[type="radio"]:checked + label:before, .checks input[type="checkbox"]:checked + label:before { background-position: left bottom; }
.checks input[type="checkbox"]#check-all + label { display: block; padding-left: 0; color: #62c1d0; line-height: 45px; text-align: center; border: 1px solid #62c1d0; border-radius: 45px; }
.checks input[type="checkbox"]#check-all + label:before { position: relative; display: inline-block; content: ""; width: 13px; height: 15px; vertical-align: middle; margin-right: 10px; background: url(data:image/png;base64,) no-repeat left top/13px; }
.checks input[type="checkbox"]#check-all:checked + label { background: #62c1d0; color: #fff; }
.checks input[type="checkbox"]#check-all:checked + label:before { background-position: left bottom; }
.checks .small input[type="checkbox"] + label { padding-left: 25px; font-weight: 400; font-size: 12px; line-height: 18px; margin-bottom: 10px; }
.checks .small input[type="checkbox"] + label:before { width: 18px; height: 18px; vertical-align: top; margin-right: 10px; background-image: url(data:image/png;base64,); background-size: 18px; }
.checks.search-check input[type="checkbox"] + label { padding: 0 12px; color: #62c1d0; font-size: 14px; line-height: 28px; text-align: center; border: 1px solid #62c1d0; border-radius: 20px; }
.checks.search-check input[type="checkbox"] + label:before { display: none; }
.checks.search-check input[type="checkbox"]:checked + label { background: #62c1d0; color: #fff; }
.checks.round input[type="radio"] + label { display: inline-block; position: relative; padding-left: 0; color: #62c1d0; border: 1px solid #62c1d0; font-weight: 700; font-size: 14px; line-height: 28px; text-align: center; margin-right: 0; margin-right: 5px; padding: 0 13px; border-radius: 20px; background: #fff; }
.checks.round input[type="radio"] + label:before { display: none; }
.checks.round input[type="radio"] + label:last-of-type { margin: 0; }
.checks.round input[type="radio"]:checked + label { background: #62c1d0; color: #fff; }
.filter-wrap { display: table; margin-bottom: 15px; }
.filter-wrap .filter { display: table-cell; padding-right: 10px; }
